<template>
  <div class="hot">
    <div class="navtop">
      <van-nav-bar
        title="热门商品"
        left-arrow
        @click-left="onClickLeft"
      />
    </div>

    <div class="content">
        <GoodsList :List="GoodsList"></GoodsList>
    </div>
  </div>
</template>

<script>
import GoodsList from "../components/GoodsList"
export default {
  name:"hot",
  components:{
    GoodsList
  },
  data(){
    return{
      GoodsList:[]
    }
  },
  created(){
    this.axios(this.baseUrl+"/groom/list/2?page=1&limit=30").then(data=>{
      this.GoodsList = data.data.data.list;

      this.$toast.clear();
    })
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    },
  },
}
</script>
<style lang="less" scoped>
.hot{
  /deep/.navtop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    .van-icon{
      color: #ff8600;
    }
    .van-nav-bar__title{
      color: #ff8600;
    }
  }
  .content{
    padding-top: 56px;
  }
}
</style>
